import React, { useState, useEffect } from "react";
import { Card, Button, Form, AutoComplete, Input, Typography, message, Modal } from "antd";
import { supabase } from "./supabase";

const { Text } = Typography;

const QueryCancelView = ({ onGoSignup }) => {
  const [form] = Form.useForm();
  const [cancelForm] = Form.useForm();
  const [nameOptions, setNameOptions] = useState([]);
  const [selectedTag, setSelectedTag] = useState(""); // 
  const [querying, setQuerying] = useState(false);
  const [result, setResult] = useState(null); // 
  const [cancelLoading, setCancelLoading] = useState(false);
  const [showCancel, setShowCancel] = useState(false);

  // AutoComplete
  useEffect(() => {
    const fetchNames = async () => {
      const { data, error } = await supabase.from("members").select("name");
      if (!error && data) {
        setNameOptions(data.map(row => ({ value: row.name })));
      }
    };
    fetchNames();
  }, []);

  //  ( status='N')
  const handleFinish = async (values) => {
    setQuerying(true);
    setResult(null);
    const inputName = values.name;
    const { data, error } = await supabase
      .from("signups")
      .select("*")
      .eq("name", inputName)
      .eq("status", "N") // 
      .maybeSingle();
    setQuerying(false);

    if (error) {
      setResult({ error: error.message });
      return;
    }
    if (!data) {
      //  &
      Modal.confirm({
        title: "",
        content: `${inputName}`,
        okText: "",
        cancelText: "",
        onOk: () => {
          onGoSignup(inputName);
        }
      });
    } else {
      setResult({
        ...data,
        name: data.name,
        battle_count: data.battle_count,
        auto_count: data.auto_count,
        status: data.status,
        cancel_code: data.cancel_code,
        id: data.id,
      });
      setShowCancel(false);
    }
  };

  //  status N  C
  const handleCancel = async (values) => {
    setCancelLoading(true);
    // 
    const { data, error } = await supabase
      .from("signups")
      .select("*")
      .eq("id", result.id)
      .eq("cancel_code", values.cancel_code)
      .eq("status", "N")
      .maybeSingle();
    if (error || !data) {
      setCancelLoading(false);
      message.error("");
      return;
    }
    // 
    const { error: updateError } = await supabase
      .from("signups")
      .update({ status: "C" })
      .eq("id", result.id);
    setCancelLoading(false);
    if (updateError) {
      message.error("" + updateError.message);
    } else {
      message.success("");
      setResult(null); // 
      setShowCancel(false);
    }
    cancelForm.resetFields();
  };

  //  & 
  return (
    <Card
      style={{
        maxWidth: 480,
        margin: "56px auto",
        borderRadius: 18,
        textAlign: "center",
        minHeight: 200,
      }}
    >
      <h2> / </h2>
      {!result ? (
        <>
          <Form
            form={form}
            layout="vertical"
            onFinish={handleFinish}
            style={{ maxWidth: 350, margin: "32px auto" }}
          >
            <Form.Item
              label=""
              name="name"
              rules={[{ required: true, message: "" }]}
            >
              <AutoComplete
                options={nameOptions}
                value={selectedTag}
                onChange={(value) => {
                  setSelectedTag(value);
                  form.setFieldsValue({ name: value });
                }}
                allowClear
                placeholder=""
                filterOption={(inputValue, option) =>
                  option.value.toLowerCase().includes(inputValue.toLowerCase())
                }
              />
            </Form.Item>
            <Button
              type="primary"
              htmlType="submit"
              size="large"
              loading={querying}
              block
              style={{
                background: "linear-gradient(90deg, #ff9800 0%, #ffb300 100%)",
                borderRadius: 10,
                fontWeight: "bold",
                fontSize: 18,
                marginTop: 8
              }}
            >
              
            </Button>
          </Form>
        </>
      ) : (
        // 
        <Card
          type="inner"
          title={<span style={{ color: "#ff9800", fontWeight: 700, fontSize: 20 }}></span>}
          style={{
            marginTop: 18,
            borderRadius: 14,
            background: "#fffbe9",
            border: "1.5px solid #ffecb3",
            boxShadow: "0 2px 8px #ffecb322",
            textAlign: "center"
          }}
          bodyStyle={{ fontSize: 17, lineHeight: 2, minHeight: 140 }}
        >
          <div><b>{result.name}</b></div>
          <div><b>{result.battle_count}</b></div>
          <div><b>{result.auto_count}</b></div>
          <div>{result.status === "N" ? <Text type="success"></Text> : <Text type="danger"></Text>}</div>
          {result.status === "N" && (
            <>
              {!showCancel ? (
                <Button
                  type="primary"
                  danger
                  style={{
                    marginTop: 30,
                    background: "#fff1e0",
                    color: "#d32f2f",
                    border: "1.5px solid #ffd54f",
                    borderRadius: 8,
                    fontWeight: "bold"
                  }}
                  onClick={() => setShowCancel(true)}
                >
                  
                </Button>
              ) : (
                <Form
                  form={cancelForm}
                  layout="inline"
                  onFinish={handleCancel}
                  style={{ marginTop: 24, justifyContent: "center", display: "flex" }}
                >
                  <Form.Item
                    name="cancel_code"
                    rules={[{ required: true, message: "" }]}
                  >
                    <Input
                      placeholder=""
                      maxLength={2}
                      style={{ width: 90, letterSpacing: 2, textAlign: "center", fontSize: 18 }}
                    />
                  </Form.Item>
                  <Form.Item>
                    <Button
                      type="primary"
                      htmlType="submit"
                      loading={cancelLoading}
                      danger
                    >
                      
                    </Button>
                  </Form.Item>
                  <Form.Item>
                    <Button
                      onClick={() => {
                        setShowCancel(false);
                        cancelForm.resetFields();
                      }}
                    >
                      
                    </Button>
                  </Form.Item>
                </Form>
              )}
            </>
          )}
        </Card>
      )}
    </Card>
  );
};

export default QueryCancelView;
